import util from '../../pages/util/util.js'
export default {
	data() {
		return {
			isShow : false
		};
	},
	created() {
		this.getData()
	},
	methods:{
		getData: async function(){
			const { data } = await util.request({},'isShowADD')
			this.isShow = data[0].isShow
		},
		addMessage() {
			uni.navigateTo({
				url: '/pages/addMessage/addMessage'
			})
		}
	}
}
